.log-in-form {
  &__brand-logo {
    display: flex;
    align-items: center;
    width: fit-content;
  }
  .circle-container {
    display: none;
    top: calc(25vh - 327px);
    left: -117px;
    z-index: -1;
    &__fill {
      position: absolute;
      width: calc(58px + 40vw);
      height: calc(58px + 40vw);
    }
    &__ring {
      width: calc(68px + 40vw);
      height: calc(68px + 40vw);
    }
  }
}
